.card-header{border-radius:2px 2px 0 0;font-size:18px;min-height:48px;position:relative;padding:4px 15px;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between;-webkit-box-align:center;-ms-flex-align:center;align-items:center}.card-header:after{content:'';position:absolute;left:0;bottom:0;right:auto;top:auto;height:1px;width:100%;background-color:rgba(0,0,0,0.12);display:block;z-index:15}html.pixel-ratio-2 .card-header:after{-webkit-transform:scaleY(.5);transform:scaleY(.5)}html.pixel-ratio-3 .card-header:after{-webkit-transform:scaleY(.33);transform:scaleY(.33)}.card-header .card-title{color:#474a4f;white-space:nowrap;text-overflow:ellipsis;overflow:hidden;word-wrap:break-word;-webkit-box-flex:1;-ms-flex:1;flex:1}


