












































.slide-transition {
  -webkit-transition-duration: .3s;
          transition-duration: .3s;
  width: 100%;
}

.slide-enter, .slide-leave {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
  overflow: hidden;
}
.app-transition {
  -webkit-transition: opacity .3s ease;
  transition: opacity .3s ease;
}

.app-enter, .app-leave {
  opacity: 0;
}



