.list{margin:16px 0;width:100%;background:#FFF;position:relative}.list:first-child{margin-top:0}.list:before{content:'';position:absolute;left:0;top:0;bottom:auto;right:auto;height:1px;width:100%;background-color:rgba(0,0,0,0.12);display:block;z-index:15}html.pixel-ratio-2 .list:before{-webkit-transform:scaleY(.5);transform:scaleY(.5)}html.pixel-ratio-3 .list:before{-webkit-transform:scaleY(.33);transform:scaleY(.33)}.list:after{content:'';position:absolute;left:0;bottom:0;right:auto;top:auto;height:1px;width:100%;background-color:rgba(0,0,0,0.12);display:block;z-index:15}html.pixel-ratio-2 .list:after{-webkit-transform:scaleY(.5);transform:scaleY(.5)}html.pixel-ratio-3 .list:after{-webkit-transform:scaleY(.33);transform:scaleY(.33)}.list .item:last-child:after{display:none}


