.toast{position:fixed;left:50%;bottom:50px;max-width:80%;border-radius:5px;background:rgba(0,0,0,0.7);color:#fff;text-align:center;padding:10px;z-index:1000;-webkit-transform:translate3d(-50%, 0, 0);transform:translate3d(-50%, 0, 0)}.toast .toast-text{font-size:1.4rem;display:block;text-align:center}.toast.toast-center{top:50%;bottom:auto;-webkit-transform:translate3d(-50%, -50%, 0);transform:translate3d(-50%, -50%, 0)}.toast-transition{-webkit-transition:opacity .3s linear;transition:opacity .3s linear}.toast-transition.toast-enter,.toast-transition.toast-leave{opacity:0}


