.form-list{margin:16px 0;width:100%;background:#FFF;position:relative}.form-list:first-child{margin-top:0}.form-list:before{content:'';position:absolute;left:0;top:0;bottom:auto;right:auto;height:1px;width:100%;background-color:rgba(0,0,0,0.12);display:block;z-index:15}html.pixel-ratio-2 .form-list:before{-webkit-transform:scaleY(.5);transform:scaleY(.5)}html.pixel-ratio-3 .form-list:before{-webkit-transform:scaleY(.33);transform:scaleY(.33)}.content-title+.form-list{margin-top:0}


