.card-footer{min-height:48px;position:relative;padding:4px 15px;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between;-webkit-box-align:center;-ms-flex-align:center;align-items:center;border-radius:0 0 2px 2px;color:#757575}.card-footer:before{content:'';position:absolute;left:0;top:0;bottom:auto;right:auto;height:1px;width:100%;background-color:rgba(0,0,0,0.12);display:block;z-index:15}html.pixel-ratio-2 .card-footer:before{-webkit-transform:scaleY(.5);transform:scaleY(.5)}html.pixel-ratio-3 .card-footer:before{-webkit-transform:scaleY(.33);transform:scaleY(.33)}.card-footer.no-border:before{display:none}


