.refresh-control{display:-webkit-box;display:-ms-flexbox;display:flex;margin:0 auto;width:36px;height:36px;color:#ff5252;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;background-color:#FFF;border-radius:50%;box-shadow:0 3px 6px rgba(0,0,0,0.16),0 3px 6px rgba(0,0,0,0.23);position:absolute;left:50%;margin-left:-18px;margin-top:24px;z-index:100}.refresh-control-animate{-webkit-transition:all .3s ease;transition:all .3s ease}.refresh-control-hide{opacity:1;-webkit-transform:translate3d(0, -63px, 0);transform:translate3d(0, -63px, 0)}.refresh-control-noshow{opacity:0;-webkit-transform:scale(.01);transform:scale(.01)}.refresh-control-refreshing{-webkit-transform:scale(1);transform:scale(1);opacity:1}


